import React, { Component } from "react";

class Projects extends Component {
  state = {
    project1_img: "/Icons/project1.webp",
    project1_name: "GATEHub",
    project1_url:
      "https://www.behance.net/gallery/101854429/GateHub-A-training-institute-website",
    project2_img: "/Icons/project2.webp",
    project2_name: "Emeraald Construction",
    project2_url:
      "https://www.behance.net/gallery/101853113/Emeraald-Construction-A-Construction-Company",
    project3_img: "/Icons/project3.webp",
    project3_name: "Burger King Redesign",
    project3_url:
      "https://www.behance.net/gallery/101852515/Burger-King-Website-redesign",
  };
  render() {
    return (
      <div className="content-wrap">
        <React.Fragment>
          <h1>Projects</h1>
        </React.Fragment>
        <div className="flex-container projects-part">
          {this.project(
            this.state.project1_img,
            this.state.project1_name,
            this.state.project1_url
          )}
          {this.project(
            this.state.project2_img,
            this.state.project2_name,
            this.state.project2_url
          )}
          {this.project(
            this.state.project3_img,
            this.state.project3_name,
            this.state.project3_url
          )}
          {/* {this.project("https://picsum.photos/400/300", "website4")} */}
          {/* {this.project("https://picsum.photos/400/300", "website5")} */}
        </div>
      </div>
    );
  }
  project(imgurl, project_name, project_url) {
    return (
      <a href={project_url} target="_blank">
        <div className="grid-items" id={project_name}>
          <img src={imgurl} alt="img" className="project-image"></img>
          {/* <span className="project-name">{project_name}</span> */}
          <div className="project-details">
            <p>{project_name}</p>
          </div>
        </div>
      </a>
    );
  }
}

export default Projects;
